import * as yup from 'yup';

export const schema = yup.object().shape({
    name: yup.string()
        .required('Nome  obrigatrio')
        .matches(/[a-zA-Z]/, 'Digite um nome vlido'),
    email: yup.string()
        .email('Emial invlido')
        .required('Email  obrigatrio'),
    password: yup.string()
        .required('Senha  obrigatrio')
        .matches(
            /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,16}$/,
            'Mnimo oito e mximo 16 caracteres, pelo menos uma letra maiscula, uma letra minscula, um nmero e um caractere especial:'    
        ),
    age: yup.number()
        .min(18, 'Idade minima de 18 anos')
        .max(100, 'Idade mximo  100 anos')
        .integer('Idade deve ser um nmero inteiro.')
        .required('Idade deve ser um nmero vlido')
        .typeError('Idade deve ser um nmero vlido')
})